import { decorate } from './logic'

const PRODUCTION_URL = 'https://mysplmqrfc.execute-api.eu-west-2.amazonaws.com/serverless_lambda_stage/weight'
const LOCAL_URL = 'http://localhost:8000'

export const fetchData = (setItems: Function, when: string) => {
  let url = PRODUCTION_URL
  if (window.location.href.indexOf('localhost') > -1) {
    url = LOCAL_URL
  }
  url += '?from_date=' + when

  fetch(url)
    .then((res) => res.json())
    .then(
      (result: any) => {
        setItems(decorate(result).reverse())
      },
      (error) => {
        console.log(error)
      }
    )
}

interface HistoricalData {
  date: string
  dateTime: string
  fat: number
  lean: number
  total: number
}

export const fetchHistorical = (when: string[]): Promise<HistoricalData[]> => {
  return new Promise((resolve, reject) => {
    let url = PRODUCTION_URL
    if (window.location.href.includes('localhost')) {
      url = LOCAL_URL
    }
    url += '?historical=' + when.join(',')

    fetch(url)
      .then((res) => {
        if (!res.ok) throw new Error(res.statusText)
        return res.json()
      })
      .then(resolve)
      .catch(reject)
  })
}
